@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
/** @format */
.App {
  text-align: center;
  font-family: "Staatliches", cursive;
  background-color: whitesmoke;
}

h2 {
  margin: 30px;
  padding: 30px;
  text-shadow: 2px 2px 5px gray;
}
button {
  box-shadow: 5px 5px 2px grey;
}

/** @format */

.customers-list {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 15px;
}
.customer-card {
  padding: 15px;
  margin: 10px;
  width: 250px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}
.customer-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 12px 40px 0 rgba(0, 0, 0, 0.19);
}
@media (max-width: 768px) {
  .customers-list {
    grid-template-columns: auto;
  }
}

/** @format */

.event-card {
  padding: 15px;
  margin: 10px;
  width: 250px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}
.event-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 12px 40px 0 rgba(0, 0, 0, 0.19);
}
.event-card-div {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 15px;
}
form {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 12px 40px 0 rgba(0, 0, 0, 0.19);
}
@media (max-width: 768px) {
  .event-card-div {
    grid-template-columns: auto;
  }
}

/** @format */

.vehicles-list {
  display: grid;
  grid-template-columns: auto auto auto;
  text-align: justify;
}
.vehicle-card {
  padding: 15px;
  margin: 10px;
  width: 250px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}
.vehicle-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 12px 40px 0 rgba(0, 0, 0, 0.19);
}
@media (max-width: 768px) {
  .vehicles-list {
    grid-template-columns: auto;
  }
}

/** @format */

.intro-p {
  margin: 15px;
  margin-top: 30px;
  padding: 15px;
}
.content-btn-div {
  margin: 20px;
  padding: 50px;
}
.content-btn {
  margin: 20px;
  padding: 50px;
}

/** @format */

h1 {
  font-weight: 300;
  font-size: 60px;
  line-height: 1.2;
  margin-bottom: 15px;
}

.showcase {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  padding: 0, 20px;
  color: white;
}

.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.video-container video {
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.video-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.content {
  z-index: 10;
  width: 30%;
}
.logo-img {
  width: 8vw;
}

